/* English */

.popup_cardEnglish {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup_inner_cardEnglish {
  position: absolute;
  left: 32%;
  right: 32%;
  top: 13%;
  bottom: 30%;
  padding: 12% 0 0 0;
  text-align: center;
  border-radius: 20px;
  background: white;
}

.iconLand_cardEnglish {
  position: absolute;
  left: 32%;
  right: 32%;
  top: 10%;
  height: 25%;
  border-radius: 20px 20px 0 0;
  background-color: #4bb543;
  z-index: 2;
}

.title_pop_cardEnglish {
  font-family: Delius;
  color: white;
  justify-content: center;
  display: flex;
  font-size: 55px;
}

.nextSteps_cardEnglish {
  font-family: Delius;
  color: black;
}

.steps_cardEnglish {
  margin: 7% 0 0 7%;
  font-family: Delius;
  line-height: 60px;
  text-align: left;
}

/* Hebrew */

.popup_cardHebrew {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup_inner_cardHebrew {
  position: relative;
  margin: -5% 25% 5% 25%;
  padding: 2% 0 7% 0;
  text-align: center;
  background: white;
}

.iconLand_cardHebrew {
  position: relative;
  margin: 5% 25%;
  background-color: #4bb543;
  z-index: 2;
}

.title_pop_cardHebrew {
  color: white;
  justify-content: center;
  display: flex;
  font-size: 350%;
}

.nextSteps_cardHebrew {
  color: black;
  font-size: 250%;
}

.steps_cardHebrew {
  margin: 5% 5% 0 0;
  line-height: 200%;
  text-align: right;
  font-size: 150%;
}

#emailAdress {
  position: relative;
  font-size: 90%;
}

@media screen and (min-width: 0px) and (max-width: 1000px) {
  .popup_inner_cardHebrew {
    margin: -5% 20% 5% 20%;
  }

  .iconLand_cardHebrew {
    margin: 5% 20%;
  }

  .title_pop_cardHebrew {
    margin-top: 0;
    font-size: 200%;
  }

  .nextSteps_cardHebrew {
    color: black;
    font-size: 150%;
  }

  .steps_cardHebrew {
    font-size: 100%;
  }

  #emailAdress {
    font-size: 90%;
  }
}
