/* English */
.cardEnglish h2 {
  font-family: HelveticaNeueExtraBold;
  font-size: 120%;
}

.cardEnglish h3 {
  font-family: HelveticaNeueExtraBold;
  font-size: 100%;
  margin: -2% 0 -1% 0;
}

.cardEnglish li {
  margin-top: -3%;
}

.cardEnglish ol {
  margin-top: -1.7%;
}

.cardEnglish {
  text-align: left;
  margin: 2% 12%;
  padding: 1%;
}

.cardEnglishParagraph {
  font-family: HelveticaNeue;
  font-size: 200%;
  color: #544333;
}

/* Hebrew */
.cardHebrew h2 {
  margin: 1.5% 0 -2.7% 0;
  font-family: FbSpoilerBold;
  font-size: 150%;
  color: #544333;
}

.cardHebrew h3 {
  margin: 1.5% 0 -2.7% 0;
  font-family: FbSpoilerBold;
  font-size: 120%;
  color: #544333;
}

.cardHebrew {
  text-align: right;
  margin: 0 12% 0 12%;
  padding: 1%;
  direction: rtl;
}

.cardHebrewParagraph {
  font-family: FbSpoilerRegular;
  font-size: 200%;
  color: #544333;
}

/* Responsive */

@media screen and (min-width: 600px) and (max-width: 1500px) {
  /* Hebrew */
  .cardHebrew h2 {
    font-size: 130%;
  }

  .cardHebrew h3 {
    font-size: 110%;
  }

  .cardHebrewParagraph {
    font-size: 170%;
  }

  /* English */
  .cardEnglishParagraph {
    font-family: HelveticaNeue;
    font-size: 130%;
    color: #544333;
  }
}

@media screen and (min-width: 0px) and (max-width: 600px) {
  /* Hebrew */
  .cardHebrew h2 {
    margin-top: 10%;
    font-size: 120%;
  }

  .cardHebrew h3 {
    font-size: 105%;
  }

  .cardHebrewParagraph {
    font-size: 95%;
  }

  /* English */
  /* English */
  .cardEnglishParagraph {
    font-family: HelveticaNeue;
    font-size: 100%;
    color: #544333;
  }
}

@media screen and (min-width: 3500px) {
  .cardHebrew h2 {
    margin: 1.5% 0 -2% 0;
  }

  .cardHebrew h3 {
    margin: 1.5% 0 -1.9% 0;
  }
}
