/* General */
#Button {
  border-radius: 1500px;
  background-color: white;
  border: solid 2px #544333;
  color: #544333;
  font-size: 30px;
}

#Button:hover {
  color: white;
  background-color: #544333;
  border: solid #544333 2px;
}

#Button:focus {
  color: white;
  background-color: #544333;
  border: solid #544333 2px;
}

/* Hebrew */
.cardHebrewButton {
  font-family: FbSpoilerRegular;
  margin-top: -3%;
  padding: 0 2% 0 2%;
}

/* English*/
.cardEnglishButton {
  text-transform: uppercase;
  font-family: Oswald;
  margin-top: 3%;
  padding: 0.5% 3% 0.5% 3%;
}

@media screen and (min-width: 0px) and (max-width: 500px) {
  #Button {
    border: solid 1px #544333;
    font-size: 100%;
  }

  #Button:hover {
    border: solid #544333 1px;
  }

  #Button:focus {
    border: solid #544333 1px;
  }
}
