.logo {
  display: block;
  height: 45%;
  width: 45%;
  background-color: white;
  border-radius: 10%;
  padding: 1%;
}

.logoDiv {
  z-index: 2;
  margin-left: 70%;
  margin-top: 4%;
  position: absolute;
}

/* Responsive */

@media screen and (min-width: 3000px) {
  .logo {
    width: 35%;
  }
}

@media screen and (min-width: 0px) and (max-width: 1000px) {
  .logo {
    width: 50%;
  }
}
