.switchLanguage {
  position: absolute;
  z-index: 2;
  border-radius: 35%;
  border: solid 2px #544333;
  margin: 0 0 0 85%;
  background-color: white;
  color: #544333;
  padding: 0.5% 1%;
}

.switchLanguage:hover {
  background-color: #544333;
  color: white;
}

.switchLanguage:focus {
  background-color: #544333;
  color: white;
}
