/* English */

.pageFooter {
  margin-top: 5%;
  background-color: white;
  color: #544333;
}

.breakLine {
  border-top: 0.5px solid #544333;
  opacity: 0.29;
}

.footerLogo {
  position: absolute !important;
  margin: -2% 0 0 -37%;
  opacity: 0.9;
  width: 60%;
  top: 100;
  left: 0;
}

.verticalLine {
  position: absolute;
  margin: 0.5% 0 0 15%;
  border-left: 0.5px solid #544333;
  opacity: 0.29;
  height: 15%;
}

.ContactUsTitle {
  font-size: 140%;
  font-family: HelveticaNeue;
  color: #544333;
  text-transform: uppercase;
  margin: 1.5% 0 0.5% 20%;
}

.footerObject {
  margin: 0.5% 0 0 21%;
  font-size: 130%;
  font-family: HelveticaNeueLight;
}

.footerMailLink {
  font-family: HelveticaNeueLight;
  color: #544333;
}

.footerMailLink:hover {
  text-decoration: none;
  color: #544333;
}

/* Hebrew */

.pageFooterHebrew {
  margin-top: 5%;
  background-color: white;
  color: #544333;
}

.breakLineHebrew {
  border-top: 0.5px solid #544333;
  opacity: 0.29;
}

.footerLogoHebrew {
  position: absolute !important;
  margin-top: -2%;
  opacity: 0.9;
  width: 60%;
  left: 40%;
}

.verticalLineHebrew {
  position: absolute;
  margin: 0.5% 0 0 81%;
  border-left: 0.5px solid #544333;
  opacity: 0.29;
  height: 15%;
}

.ContactUsTitleHebrew {
  text-align: right;
  font-size: 170%;
  font-family: FbSpoilerRegular;
  color: #544333;
  margin: 1% 22% 0.5% 0;
}

.footerObjectHebrew {
  text-align: right;
  margin: 0 23% 0 0;
  font-size: 130%;
  font-family: FbSpoilerRegular, Oswald, Arial;
}

/* Responsive */

@media screen and (min-width: 500px) and (max-width: 1500px) {
  .footerLogoHebrew {
    width: 75%;
    left: 28%;
  }

  .footerLogo {
    width: 75%;
    left: -13%;
  }
}

@media screen and (min-width: 0px) and (max-width: 500px) {
  .verticalLineHebrew {
    height: 10%;
  }

  .footerMailLink {
    font-size: 85%;
  }

  .ContactUsTitleHebrew {
    font-size: 100%;
  }

  .footerObjectHebrew {
    font-size: 90%;
  }

  .ContactUsTitle {
    font-size: 100%;
  }

  .footerObject {
    font-size: 90%;
  }
}
